@import './variables.scss';

.contact {
  width: 60%;
  margin: 50px auto;

  button {
    color: white;
    background-color: $marigold;
    border: none;
  }

  .card {
    margin-bottom: 15px;
    background: #f7f7f7;
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
    padding: 30px;

    a {
        color: black;
    }
  }

  .row-margin {
      margin-top: 1rem;
  }
}

@media only screen and (max-width: 768px) {
  .contact {
    width: 60%;
  }
}

@media only screen and (max-width: 480px) {
  .contact {
    width: 90%;
  }
}
