.carousel.carousel-slider {
    height: 40rem;
}

.responsive-image {
    max-width: 100%;
    height: auto;
}

@media only screen and (max-width: 768px) {
    .carousel.carousel-slider {
        height: 25rem;
    }
}

@media only screen and (max-width: 480px) {
    .carousel.carousel-slider {
        height: 15rem;
    }
}